import React from 'react';
import { Route, Switch } from 'react-router-dom';
import { Dashboard } from '../Components/Dashboard';
import { SharePage } from '../Components/SharePage';
import { ManageUser } from '../Components/ManageUser';
import { Login } from '../Components/Login';
import { NotFound } from '../Components/NotFound';
import { Registration } from '../Components/Registration';
import { UploadPage } from '../Components/UploadPage';
import { EditUserPage } from '../Components/EditUserPage';
import { OpenFilePage } from '../Components/OpenFilePage';
const Allroutes = () => {
    return (
        <div>
            <Switch>
                <Route exact path="/">
                    <UploadPage />
                </Route>
                <Route exact path="/registration">
                    <Registration />
                </Route>
                <Route exact path="/login">
                    <Login />
                </Route>
                <Route exact path="/dashboard">
                    <Dashboard />
                </Route>
                <Route exact path="/manageUser">
                    <ManageUser />
                </Route>
                <Route exact path="/edit" component={EditUserPage}>
                    <EditUserPage />
                </Route>
                <Route exact path="/open" component={OpenFilePage}>
                    <OpenFilePage />
                </Route>
                <Route exact path="/share" component={SharePage}>
                    <SharePage />
                </Route>

                <Route>
                    <NotFound />
                </Route>
            </Switch>
        </div>
    )
}

export { Allroutes }