import React, { useState } from 'react';
import axios from "axios"
import { useHistory } from 'react-router';
import TextField from '@material-ui/core/TextField';
import styles from "../Styles/Form.module.css";
import { Button, Grid, Typography } from '@material-ui/core';



const EditUserPage = () => {
    const history = useHistory()
    const [name, setName] = useState("")
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")

    const id = window.location.href.split("?")[1]

    const handleClick = () => {
        let config = {
            "name": name,
            "email": email,
            "password": password
        }

        axios.patch(`/api/users/:${id}`, config)
            .then((res) => {
                if (res.status === 200) {
                    alert("Update User Success");
                    redirectPage()
                } else {
                    alert("Fail To Update User")
                }
            })
            .catch((err) => {
                alert("Update Failed")
                console.log(err);

            })
    }
    const redirectPage = () => {
        history.push('/manageUser')
    }
    return (
        <Grid container justify="center" className={styles.loginWrapper}>

            <Grid className={styles.loginCard} style={{ marginTop: "50px" }} container align="center" direction="column" md={3} sm={5} xs={9}>
                <Typography className={styles.editTag} variant="h5" style={{ fontWeight: "bold", color: "#283593", marginBottom: "10px" }}>Edit User</Typography>
                <TextField
                    id="outlined-primary"
                    variant="outlined"
                    required={true}
                    label="Name"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                />
                <Grid className={styles.space} />
                <TextField
                    type="email"
                    id="outlined-primary"
                    variant="outlined"
                    required={true}
                    label="Email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                />
                <Grid className={styles.space} />
                <TextField
                    type="password"
                    id="outlined-primary"
                    variant="outlined"
                    required={true}
                    label="Password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                />
                <Grid className={styles.space} />
                <Button variant="contained" color="primary" onClick={handleClick}>Edit</Button>
            </Grid>

        </Grid >
    )
}

export { EditUserPage }